/* Top Banner
 * ========================================================================== */
.section-wrapper-1 {
  @include background-base($navy);
}
.main-banner {
  display: flex;
  flex-flow: column wrap;
  height: 100%;
  min-height: 40vh;
  overflow: hidden;
  position: relative;
  padding: 6em 1em;
  align-items: center;
  justify-content: center;
  .hero-text {
    text-align: center;
    line-height: 1.2;
  }
  .subtitle {
    padding: 20px 0;
    font-size: 1em;
    letter-spacing: 2px;
    font-style: italic;
    font-weight: 100;
    text-align: center;
  }
}
.button-container {
  display: inherit;
  width: 100%;
  margin-top: 30px;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

@include for-size(tablet, up) {
  .main-banner {
    padding: 8em 1em;
  }
}
@include for-size(phone-lg, up) {
  .button-container {
    flex-direction: row;
  }
}
/* Resources Section
 * ========================================================================== */
.section-wrapper-2 {
  position: relative;
  &::after {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    z-index: -1;
    @include background("/src/images/resources-bg.jpg");
    background-color: rgba(255, 255, 255, 0.25);
    background-blend-mode: lighten;
  }
}

.resources {
  margin: 0 auto;
  height: 100%;
  min-height: 60vh;
  overflow: hidden;
  padding: 4em 1em;
  z-index: 4;
}

.banner-description {
  padding-top: 2em;
}

.card-columns {
  margin: 4em auto 2em;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(4, 1fr);
  grid-template-rows: auto;
  width: fit-content;
  grid-column-gap: 1em;
  grid-row-gap: 0.75em;

  .resource-link {
    text-align: center;
    margin: 2em auto 0;
  }
}

@include for-size(tablet, up) {
  .card-columns {
    margin: 5em auto 2em;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
    grid-template-rows: auto;
    width: fit-content;
    grid-column-gap: 0.75em;
    grid-row-gap: 0.5em;
  }
}

@include for-size(tablet-lg, up) {
  .card-columns {
    margin: 5em 0 2em;
  }
}

/* Search
 * ========================================================================== */
.section-wrapper-3 {
  @include background-base($white);
}

.benefits,
.blocks {
  margin: 5em auto;
  padding: 2em 1em;
  display: flex;
  flex-flow: row wrap;
  justify-content: center;

  .right {
    height: 100%;
    display: none;
  }
  .left {
    h3 {
      margin-bottom: 0.5em;
    }

    .accent {
      font-style: italic;
      border-bottom: 2px solid $black;
      width: fit-content;
      margin-bottom: 5px;
    }
    p {
      margin: 0.5em 0 2em 1.25em;
    }
  }
}
.benefit-container {
  padding: 0.5em 1em;
}

@include for-size(tablet, up) {
  .benefits,
  .blocks {
    margin: 5em auto;
    padding: 2em 0;
    display: flex;
    flex-flow: row wrap;

    .right {
      display: block;
      flex-basis: 50%;
      padding-right: 2em;
      height: 100%;
    }
    .left {
      flex-basis: 50%;
      h3 {
        margin-bottom: 0.5em;
      }

      .accent {
        font-style: italic;
        border-bottom: 2px solid $black;
        width: fit-content;
        margin-bottom: 5px;
      }
      p {
        margin: 0.5em 0 2em 1.25em;
      }
    }
  }
}

/* Block Section
 * ========================================================================== */

.section-wrapper-3 {
  @include background-base($white);
}

.block-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 10px 15px;
  min-height: 300px;
  margin-bottom: 2em;
  width: 100%;
  box-shadow: 0 3px 7px -1px rgba(#000, 0.14);
  margin-bottom: 1.6%;
  position: relative;
  border-radius: 4px;

  &#cta {
    &::after {
      content: "";
      position: absolute;
      height: 100%;
      top: 0;
      right: 0;
      left: 0;
      bottom: 0;
      z-index: -1;
      @include background("/src/images/modu-find-lender.jpg");
      background-color: rgba(255, 255, 255, 0.25);
      background-blend-mode: lighten;
    }
  }
  &#resources {
    &::after {
      content: "";
      position: absolute;
      height: 100%;
      top: 0;
      right: 0;
      left: 0;
      bottom: 0;
      z-index: -1;
      @include background("/src/images/modu-resources.jpg");
      background-color: rgba(255, 255, 255, 0.25);
      background-blend-mode: lighten;
    }
  }
  &#va-loan {
    &::after {
      content: "";
      position: absolute;
      height: 100%;
      top: 0;
      right: 0;
      left: 0;
      bottom: 0;
      z-index: -1;
      @include background("/src/images/modu-va-loan.jpg");
      background-color: rgba(255, 255, 255, 0.25);
      background-blend-mode: lighten;
    }
  }
}
.right.right-blocks {
  padding-right: 1em;
}
.left.left-blocks {
  padding-left: 1em;
}

.tall {
  height: 100%;
  text-align: center;
}
/* Search Section
 * ========================================================================== */
.section-wrapper-5 {
  position: relative;
  &::after {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    z-index: -1;
    @include background("/src/images/modu-va-loan.jpg", cover, center center);
    background-color: rgba(255, 255, 255, 0.85);
    background-blend-mode: lighten;
  }
}

.search {
  padding: 2em;
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  align-items: center;
  justify-content: center;
}

.search-container {
  width: 100%;
  display: block;
  margin: 2em auto 0;
}

input#search-bar {
  margin: 0 auto;
  width: 100%;
  height: 45px;
  padding: 0 20px;
  font-size: 1rem;
  border: 1px solid #d0cfce;
  outline: none;
  &:focus {
    border: 1px solid #008abf;
    transition: 0.35s ease;
    color: #008abf;
    &::-webkit-input-placeholder {
      transition: opacity 0.45s ease;
      opacity: 0;
    }
    &::-moz-placeholder {
      transition: opacity 0.45s ease;
      opacity: 0;
    }
    &:-ms-placeholder {
      transition: opacity 0.45s ease;
      opacity: 0;
    }
  }
}

.search-icon {
  position: relative;
  float: right;
  width: 75px;
  height: 75px;
  top: -62px;
  right: -45px;
}

@include for-size(tablet, up) {
  .search-container {
    flex-basis: 60%;
    padding-left: 2em;
    margin: 2em auto;
  }
}
